
.graph-container{
    width: 100%;
    margin:auto;
    margin:50px 30px 30px ;
}

.chart{
    position: relative;

    ul{
        list-style-type: none;
        margin:0;
        padding: 0;
    }
    
    .numbers {
        li{
            position: absolute;
        }
    }
    
    .numbers-horizontal{
        align-items: flex-end;
        position: absolute;
        display: flex;
        bottom: -30px;
        justify-content: space-between;
        z-index: 1;
        width: 100%;

        li{
            text-align: center;
            width:calc(100% / 7);
            // width: calc(100% - 20px);
        }

        li:nth-child(even){
            margin: 0px 10px;
        }
    }

    
    .bars{
        position: absolute;
        display: flex;
        align-items: flex-end;
        height: 100%;
        bottom: 0;
        width: 100%;
        z-index: 2;

        li{
            width:calc(100% / 7);
            height: 100%;
            align-items: flex-end;
            display: flex;
        }

        li:not(:last-child){
            border-right: 2px solid $highlightBlack;
        }

        // li:nth-child(even){
        //     margin: 0px 10px;
        // }


        .bar{
            position: relative;
            height: 50%;
            width:100%;
            bottom: 0px;
            display: flex;
            justify-content: center;
            align-items: flex-end;

            .views-popup{
                display: none;
                position:absolute;
                top: -30px;
                left: -60px;
                flex-direction: column;
                padding:10px;
                border-radius: 9px;
                background: #272b30 !important;

                .date{
                    white-space: nowrap;
                }

                .views{
                    display: flex;
                    align-items: center;
                    >div{
                        width:10px;
                        height: 10px;
                        margin-right: 5px;
                        border-radius: 2px;
                    }

                }

            }

            .bar-increase{
                width: 60%;
                height: 100%;
                border-radius: 9px;
                transition: background 0.2s ease-out;
            }

        }

        @for $i from 1 through 3 {
            .bar:nth-child(#{length($base-color)}n+#{$i}){
                .views{
                    >div{
                        background: nth($base-color , $i);
                    }
                }
            }
        }

        .bar:hover .bar-increase{
            background: $primaryBlue !important;
        }

        .bar:hover .views-popup{
            display: block;
        }

        @for $i from 1 through 3 {
            .bar:nth-child(#{length($base-color)}n+#{$i}){
                >div{
                    background: nth($base-color , $i);
                }
            }
        }
    }

}
