@import './colors.scss';

*{
    margin:0;
    padding:0;
    box-sizing:border-box;
    text-decoration: none;
    list-style-type:none; 
}

body{
    background: $black;
}

h1,h2,h3,h4,h5,h6,a{
    color:$white;
    font-family: 'Ubuntu' , sans-serif;
}

p,li,a,label{
    font-family: 'Open Sans' , sans-serif;

}

.light{
    opacity: 0.5;
}

.section{
    background: $offBlack;
    padding: 20px;

    .heading-with-bar{
        padding-bottom: 20px;
    }
}


// these are used in the figures - change
.green , .red{
    font-size: 0.8em;
    padding: 2px 8px;
    border-radius:5px;
}

.green{
    background:$secondaryGreen;
    color:$primaryGreen;
    font-weight: 600;
}

.red{
    background: lighten($secondaryRed,10%);
    color:$primaryRed;
    font-weight: 600;
}

.like{
    background: $primaryRed;
}

.chat{
    background: $primaryBlue;
}


