.sidebar-container{
    width: 270px;
    background:$offBlack;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;


    >div{
        padding:20px 30px;   

        .logo{
            padding: 20px;
            width: 10px;
            border-radius: 100px;
            background: $white;
            height: 10px;
            position: relative;

            svg{
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto;
            }
        }
    }

    .sidebar-nav{

        padding: 0px 10px;

        >li{
            position: relative;
            font-size: 1.2em;
            border-radius: 7px;
            margin-bottom: 8px;
            // border: solid transparent 1px;
            overflow: hidden;

            >a{
                padding: 10px 20px;
                color:$gray;
                display:flex;
                align-items: center;
                font-weight: 500;
                font-size: 0.8em;
                justify-content: space-between;

                svg{
                    min-width: 20px;
                    fill:$gray;
                    margin-right: 10px; 
                }
            }

            .dropdown{
                position: relative;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                background: $offBlack;
                color: $white;
                font-size: 0.8em;
                padding:0px 46px;
                height: 0px;
                opacity: 0;
                z-index: 1;

                >li{
                    background: $offBlack;
                    border-left: $offBlack 10px solid;
                    margin: 5px 0px ;
                    position: relative;
                    
                    &:not(:last-child):after{
                        position: absolute;
                        content: '';
                        height: 6px;
                        width: 7px;
                        top:0;
                        left: -25px;
                        bottom:0;
                        border-left: solid 2px $gray;
                        border-bottom: solid 2px $gray;
                        border-bottom-left-radius: 5px;
                        margin: auto;
                    }
                }

            }

            .dropdown::after{
                content:'';
                position: absolute;
                background: $offBlack;
                top:0;
                left: 0;
                height: 90%;
                width: 8px;
                left:30px;
                border-left:solid $gray 2px;
                border-bottom:solid $gray 2px;
                border-bottom-left-radius: 5px;
                z-index: -1;
            }
        }

       >li:hover{
            >a{
                background: $highlightBlack;
                border-top: solid $gray 1px;
                border-bottom: solid $black 1px;
                color: $white;

                svg{
                    min-width: 20px;
                    fill:$white;
                    margin-right: 10px; 
                }

            }
        }

    }


}