.selection {
  display: flex;
  justify-content: space-between;
}
.selection label {
  font-weight: 600;
}
.selection .check {
  margin-bottom: 10px;
}
.selection .check input[type=checkbox] {
  position: relative;
  appearance: none;
  height: 20px;
  width: 20px;
  background: transparent;
  border: 1px #272b30 solid;
}
.selection .check input[type=checkbox]::after {
  content: "\f00c";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 10px;
  width: 10px;
  height: 10px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  display: none;
}
.selection .check input[type=checkbox]:checked {
  background: #2a85ff;
}
.selection .check input[type=checkbox]:checked::after {
  display: block;
}

.secondary label {
  opacity: 0.5;
}

.btn {
  background: transparent;
  color: #fcfcfc;
  padding: 11px 14px;
  border: #272b30 2px solid;
  border-radius: 9px;
  font-size: 0.8em;
  transition: border 0.5s ease-out;
}

.full-width {
  width: 100%;
}

.btn:hover:not(.fill) {
