  height: 10px;
  margin-right: 5px;
  border-radius: 2px;
}
.chart .bars .bar .bar-increase {
  width: 60%;
  height: 100%;
  border-radius: 9px;
  transition: background 0.2s ease-out;
}
.chart .bars .bar:nth-child(3n+1) .views > div {
