}
.chart .bars .bar:hover .bar-increase {
  background: #2a85ff !important;
}
.chart .bars .bar:hover .views-popup {
  display: block;
}
.chart .bars .bar:nth-child(3n+1) > div {
  background: #b5e4ca;
}
.chart .bars .bar:nth-child(3n+2) > div {
  background: #ffbc99;
}
.chart .bars .bar:nth-child(3n+3) > div {
  background: #b1e5fc;
}

.line-chart {
  list-style: none;
  margin: 0;
  padding: 0;
}

.point {
  width: 10px;
  height: 10px;
  border: 2px solid #2a85ff;
  border-radius: 50%;
  position: absolute;
  background: #1a1d1f;
  z-index: 1;
}

.hypline {
  position: absolute;
  height: 3px;
  transform-origin: left bottom;
  background: #2a85ff;
}

.numbers {
  list-style: none;
  margin: 0;
  padding: 0;
}

.line-graph .numbers li {
  position: absolute;
  width: 100%;
  border-bottom: 2px solid #272b30;
}

@keyframes anidown {
  0% {
    transform: translateY(-100vh);
  }
  95% {
    transform: translateY(4vh);
  }
  100% {
    transform: translateY(0vh);
  }
}
.clicked {
  animation: popout forwards 0.6s ease-in-out;
  display: block !important;
}

@keyframes popout {
  0% {
    transform: scale(0);
  }
  80% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.highlighted {
  position: relative;
  overflow: hidden;
  border-top: solid #474e53 1px;
  border-bottom: solid #1a1c1e 1px;
  box-shadow: 0px 0px 2px #111315;
}
.highlighted > div {
  position: relative;
  z-index: 1;
}

.highlighted::after {
  content: "";
  position: absolute;
  padding: 5px;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #272b30;
}

.rotate {
  transform: rotate(180deg);
  transition: all 0.5s ease-out;
}

.bak {
  transform: rotate(0deg);
  transition: all 0.5s ease-out;
}

@keyframes slide {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}
.drp-nav {
  transition: all 0.5s ease-out;
  display: block;
  animation: drop 0.5s forwards ease-out;
}

@keyframes drop {
  0% {
    height: 0%;
  }
  100% {
    opacity: 1;
    height: 100%;
  }
}
.bar-increase {
  animation: increase 0.9s forwards ease-in;
}

@keyframes increase {
  0% {
    height: 0%;
  }
  100% {
    height: 100%;
  }
}
