
.line-chart{
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  
.point{
    width:10px;
    height:10px;
    border:2px solid $primaryBlue;
    border-radius:50%;
    position: absolute;
    background: $offBlack;
    z-index:1;
}


.hypline{
    position: absolute;
    height:3px;
    transform-origin: left bottom;
    background:$primaryBlue;
}

.numbers{
    list-style: none;
    margin: 0;
    padding: 0;
}

.line-graph .numbers li{
    position: absolute;
    width: 100%;
    border-bottom: 2px solid $highlightBlack;
}