.refunds-container{
    display: flex;
    padding-bottom: 20px;

    .icon{
        width: 50px;
        height: 50px;
        background: $secondaryRed;
        border-radius:100%;
        margin-right: 10px;
        position: relative
    }

    .icon::after{
        position: absolute;
        content: '\f291';
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: $primaryRed;
        height: 19px;
        width: 17px;
        top:0;
        left:0;
        right: 0;
        bottom: 0;
        margin: auto;
    }

    .message{
        width:calc(100% - 50px);
    }
}