.notification-block{
    display: flex;
    height: 100%;
    align-items: center;
    .notification-content{
        width:calc(100% - 50px);
    }
}


.image{
    position: relative;
    z-index: 2;
    background-size:cover;
    background-position: center;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    margin-right: 10px;

    .icon-cont{
        position: absolute;
        right: -3px;
        bottom: -3px;
        width:25px;
        height: 25px;
        border: solid $highlightBlack 2px;
        border-radius: 100%;

        svg{
            right: 0;
            position: absolute;
            bottom: 0;
            left: 0;
            top: 0;
            margin: auto;
            width: 10px !important;
        }
    }
}


.comment-block{
    display:flex;

    .comment-content{
        width:calc(100% - 50px);
        background: $offBlack;
        position: relative;

        .commenter{
            flex-direction: row;
        }
    

        .additional-actions-icons , .additional-actions-words{
            justify-content: space-between;
            display: flex;
            padding-top: 8px;
            >div{
                height: 15px;
                width: 15px;
    
                >svg{
                    width: 100%;
                    height: 100%;
                    color:$gray;
                }
            }
        }

        .additional-actions-words{
            width: 100px;
        }

        .additional-actions-icons{
            width: 100%;
        }
    
        .reply-container{
            display: flex;
            margin-top: 20px;
    
            .image{
                width: 30px;
                height: 30px;
            }
    
            .replying-user{
                width: calc(100% - 40px);

                .commenter{
                    flex-direction: column;

                    .buttons{
                        button{
                            width: 65px;
                            padding:6px;
                        }
                    }

                   .input-container{
                        display: flex;
                        justify-content:flex-start;
                        margin-bottom:10px;
                        .user {
                            height: 20px;
                            p{
                                color:$primaryBlue
                            }
                        }
                        form{
                            width: 50%;
                            align-items: center;
                            display: flex;
                            input{
                                background:transparent;
                                border:none;
                                color:$gray;
                            }
                            input:focus{
                                outline:0;
                            }
                        }
                    }
                }
                
            }
        }

    }
}

.commenter{
    display: flex;
    justify-content: space-between;
}

// .notification-block:not(:last-child) , .comment-block:not(:last-child){
//     padding-bottom:20px;
// }

.notification-block, .comment-block{
    margin-bottom: 20px;
    padding-bottom:20px;
    border-bottom: 1px solid $highlightBlack;
}


.action{
    h5:first-child{
        margin-right: 5px;
    }
}

.user , .action{
    display: flex;
}

.user{
    display: flex;
    align-items: center;
    >*:first-child{
        margin-right:5px;
    }
}

.textbox{
    display: inline;
    min-width: 300px;
    max-width:600px;
    min-height: 50px;
    word-break: break-all;
    border-radius: 9px;
    padding: 7px 10px 10px 5px;
}

.textbox::before{
    content:'@Mario ';
    color:$primaryBlue
}

.textbox:focus{
    border: $highlightBlack 2px solid;
    outline: 0px solid transparent;
}
