.main-content{

    padding-top: 80px;


    .main-content-container{
        padding: 20px 40px;

        .heading{
            padding: 30px 0;
        }

        .main-content-body{
            display: flex;
            flex-wrap: wrap;
            flex-shrink: 1;
            width: 100%;



            // left board / right board
            >div{
                flex-grow: 1;
                // min-width: 400px;
                // width:calc((100% / 2) - 20px);
                display: flex;
                height: fit-content;

                .dashboard{
                    color:$white;
                    width: 100%;
                    >div{
                        border-radius: 9px;
                        margin-bottom: 10px;
                    }

                    .figures , .send-message{
                        display: flex;
                        justify-content: space-between;
                    }

                    .figures{
                        background: $black;
                        border-radius: 9px;
                        padding: 5px;
                        flex-wrap: wrap;
                        flex-shrink: 1;


                        .figure{
                            width:49%;
                            min-width: 200px;
                            flex-grow: 1;
                            padding: 10px;
                            display: flex;
                            justify-content: space-between;
                            border-radius:9px;
                            
                            .customers , .income{
                                display: flex;
                                // align-items: baseline;
                                .icon{
                                    width: 50px;
                                    height: 50px;
                                    background: pink;
                                    align-self: center;
                                    border-radius: 100px;
                                    margin-right: 10px;
                                }



                                span{
                                    font-size: 0.6em; 
                                }
                            }
                        }
                    }
                }
            }


            .main-content-body-left{
                width: calc(50% - 10px);
                margin-right: 10px;
                min-width: 300px;
            }

            .main-content-body-sidebar{
                min-width: 300px;
                width: 20%;
            }
        }
    }
}