.btn{
    background: transparent;
    color: $white;
    padding: 11px 14px;
    border: $highlightBlack 2px solid;
    border-radius: 9px;
    font-size: 0.8em;
    transition: border 0.5s ease-out;
}

.full-width{
    width: 100%;
}

.btn:hover:not(.fill){
    border: $gray 2px solid;
}

.fill{
    background: $primaryBlue;
    color:$white;
}

.outline{
    border: $white 1px solid;
}

.disabled{
    opacity: 0.5;
}