.hamburger-icon{
    width: 40px;
    height: 24px;
    align-self: center;
    >div{
        height: calc((100% - 15px) / 3);
        width: 100%;
        background: $white;
        margin-bottom: 5px;
    }
}

.mobile-list{
    display: none;
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    background: $offBlack;
    height: 100%;
    overflow: hidden;
    z-index: 999;
}

.block{
    display: block;
}
 
 
 /* Extra small devices (phones, 600px and down) */
 @media only screen and (max-width: 992px) and (min-width: 300px) {
    .sidebar-container{
        display: none;
    }
    .main-body-container{
        width: 100%;

        .main-content{
            .main-content-container{
                padding: 20px 20px;
            }
        }

        .top-bar{
            width: 100%;

            .create-btn{
                display: none;
            }

            .top-bar-inner-cont{
                .top-bar-side-nav{
                    justify-content: flex-end;
                    min-width: 150px;
                }

                .hamburger-icon{
                    display: block;
                }

                .search-box-cont{
                    .search-box::placeholder{
                        color: transparent;
                    }
                }
            }
        }
    }
 }

 /* Small devices (portrait tablets and large phones, 600px and up) */
 @media only screen and (min-width: 600px) {
     .hamburger-icon{
         display: none;
     }
 }
 
 /* Medium devices (landscape tablets, 768px and up) */
 @media only screen and (min-width: 768px) {
 }
 
 /* Large devices (laptops/desktops, 992px and up) */
 @media only screen and (min-width: 992px) {
    .sidebar-container{
        display: block;
    }
 }
 
 /* Extra large devices (large laptops and desktops, 1200px and up) */
 @media only screen and (min-width: 1200px) {
    .sidebar-container{
        display: block;
    }
 } 