.welcome-customers{
    >div{
        margin:40px 0px;
    }

    .send-message{
        align-items: center;

        >div:first-child{
            width: 200px;
        }
    }

    .customers-list{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        .individual-customer{
            display: flex;
            flex-direction: column;
            align-items: center;
            .customer-image{
                position: relative;
                width:60px;
                height:60px;
                background: #272b30;
                background-size: cover;
                background-position: center;
                border-radius: 100%;
                margin-bottom: 5px;
            }

            .next::after{
                position: absolute;
                border-radius: 100%;
                height: 15px;
                width: 8px;
                content:"\f054";
                left:0;
                top:0;
                right: 0;
                bottom:0;
                margin:auto;
                font-weight: 900;
                font-family: "Font Awesome 5 Free";
            }
        }
    }
}