.tips-container{
    p{
        margin-bottom: 20px;
    }
    .tips{
        display:flex;
        flex-wrap:wrap;
        flex-shrink: 1;

        .tip{
            height: 80px;
            min-width: 250px;
            width: 50%;
            display:flex;
            flex-grow: 1;
            margin-bottom: 20px;
            padding-bottom: 20px;
            align-items: center;

            .image{
                position: relative;
                width: 80px;
                height: 80px;
                background: $lightGray;
            }

            .image::after{
                position: absolute;
                color:$gray;
                content:'\f86d';
                width: 20px;
                height: 20px;
                left:0;
                top:0;
                right: 0;
                bottom:0;
                margin:auto;
                font-weight: 900;
                font-family: "Font Awesome 5 Free";
            }

            .tip-link{
                display: flex;
                flex-direction: column;

                .more-info , .time{
                    display: flex;
                }

                .more-info{
                    padding-top:10px;

                    .tag{
                        background: $primaryGreen;
                        padding:2px 5px;
                        border-radius: 5px;
                    }
                }

                .time{
                    align-items: center;
                    margin-left: 10px;
                    position: relative;
                    .avatar{
                        background-image: url('https://images.unsplash.com/photo-1497215728101-856f4ea42174?ixlib=rb-1.2.1&ixid=MnwxMjA3fDF8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80');
                        position: absolute;
                        background-size: cover;
                        width: 28px;
                        height: 28px;
                        border-radius: 100%;
                        z-index: 1;
                    }

                    .description{
                        position: absolute;
                        border-radius: 0px 9px 9px 0px;
                        font-size: 14px;
                        min-width: 90px;
                        left: 18px;
                        padding-left: 10px;
                        border: solid $highlightBlack 2px;
                    }
                }
            }
        }
    }
}