.sign-in{
    .form-block{
        width: 100%;
    }
}

.sign-up{
    .form-block{
        width: 70%;
    }
}
.sign-up , .sign-in{
    color:white;
    position: fixed;
    height: 100%;
    width: 100%;
    overflow: hidden;
    z-index: 9999;

    .sign-up-container , .sign-in-container{
        display:flex;
        height: 100%;

        .form-block{
            min-width: 500px;
        }

        .info-board{
            width: 30%;
            background: $black;
            position: relative;

            .info-board-content{
                h3{
                    text-align: center;
                    margin:40px 0px
                }
                .image{ 
                    width:50px;
                    height:50px;
                    background: $white;
                    border-radius: 100%;
                }

                ul{
                    li{
                        margin-bottom:20px;
                    }
                    li:before{
                        content:"\f058";
                        font-family: "Font Awesome 5 Free";
                        font-weight: 600;
                        color: $primaryGreen;
                    }
                }
            }
        }

        .info-board-content , .form-block-content{
            display: flex;
            flex-direction: column;
            align-items:center;
            position: absolute;
            top:0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            width: 50%;
            max-width: 500px;
            min-width: 300px;
            height: 400px;
            justify-content: space-around;
        }

        .form-block{
            background: $offBlack;
            position: relative;

            .have-an-account{
                position: absolute;
                right: 20px;
                top:20px;
            }

            .form-block-content{
                .heading{
                    >*{
                        margin-bottom:5px;
                    }
                }
                hr{
                    width:100%;
                    border: solid 1px $highlightBlack;
                }
                .third-party-btns{
                    width: 100%;
                    display: flex;
                    justify-content: space-between;

                    .btn{
                        width: 49%;
                    }
                }
            }

            .heading{
                align-self: flex-start;
            }

            form{
                >*{
                    margin-bottom:15px;
                }
                width: 100%;
                display: flex;
                flex-direction: column;
            }
        }
    }
}

.slide-left{
    animation:slideleft 0.5s forwards ease-out;
}

@keyframes slideleft{
    0%{
        width: 30%;
    }

    100%{
        width: 0%;
    }
}


.expand{
    animation: expand 0.5s forwards ease-out;
    max-width:unset !important;
    min-width: unset !important;
    width: 100% !important;
}

@keyframes expand{
    0%{
        width: 70%;
    }

    100%{
        width: 100% !important;
    }
}