.top-bar{
    z-index: 9;
    position: fixed;
    width: calc(100% - 270px);
    top: 0;
    background:$offBlack;
    border-left: solid 2px $black;
    padding:20px 40px;
    .top-bar-inner-cont{
        display:flex;
        justify-content: space-between;
        width: 100%;
        height: 40px;

        form , ul{
            display: flex;
        }

        .search-box-cont{
            width:40%;
            max-width:350px;
            min-width: 100px;
            position:relative;
            height: 100%;
            
            .search-box{
                position: absolute;
                background: $highlightBlack;
                border:none;
                color: $white;
                top:0;
                left: 0;
                width:100%;
                height: 100%;
                border-radius:9px;
                padding-left: 40px;
            }

            .search-box:focus{
                border: $primaryBlue 2px solid;
            }
            
            .search-box:focus-visible{
                border:none;
            }

            input::placeholder{
                color: $white;
            }


            .search-btn{
                position: absolute;
                left: 10px;
                top:0;
                bottom:0;
                background: transparent;
                border:none;
                height: fit-content;
                margin:auto 0;

                svg{
                    color: $white;
                    width: 20px;
                    height: 20px;
                }
            }

            .shortcut{
                position:absolute;
                right: 0;
                top:0;
                bottom:0;
                margin:auto 0;
                height: 70%;
                width: 40px;
                border:none;
                border-radius:5px;
                color:$white;
                background: $black;
                margin-right: 10px;
            }
        }

        .top-bar-side-nav{
            width:30%;
            min-width: 260px;
            max-width: 300px;
            justify-content: space-between;
            align-items: center;
            li{
                margin-right:10px;
                
                a{


                    .icon{
                        position: relative;
                        width: 20px;
                        height: 20px;
                    
                        svg{
                            width: 100%;
                            height: 100%;
                        }

                        .notifications{
                            position: absolute;
                            width:8px;
                            height: 8px;
                            background: red;
                            top: -3px;
                            right:-3px;
                            border-radius:5px;
                        }
                    }

                    .notifications-cont{
                        position: relative;
                        
                        .triangle{
                            width: 0;
                            height: 0;
                            border-left: 10px solid transparent;
                            border-right: 10px solid transparent;
                            border-bottom: 20px solid $black;
                            position: absolute;
                            top: -20px;
                            left: 180px;
                        }

                        .notification-dropdown{
                            position: absolute;
                            display: none;
                            left: -180px;
                            top: 60px;
                            background: $black;
                            padding: 23px 20px;
                            width: 280px;
                            border-radius: 9px;
                            border: $highlightBlack 0.1px solid;

                            .comment-block{
                                padding:10px 0px;
                                
                            }

                            // .comment-block:not(:last-child){
                            //     border-bottom: 1px $highlightBlack solid;
                            // }

                            button{
                                width: 100%;
                            }
                        }
                    }


                    .avatar{
                        height: 50px;
                        width: 50px;
                        border-radius: 100%;
                        background-image:url('https://images.unsplash.com/photo-1494790108377-be9c29b29330?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80');
                        background-size: cover;
                        background-position:center;
                    }
                }
            }

            .create-btn{
                height: 100%;
                
                button{
                    background: $primaryBlue;
                    height: 100%;
                    border: none;
                    padding: 0 16px;
                    display: flex;
                    align-items: center;
                    width: 110px;
                    justify-content: space-between;
                    color:$white;
                    border-radius: 9px;
                    font-weight: 600;

                    div:first-child{
                        display: flex;
                        align-items: center;


                        svg{
                            width: 20px;
                            height: 20px;
                        }
                    }
                }
            }
        }
    }
}