.heading-with-bar{
    height: 60px;
    display: flex;
    justify-content: space-between;

    .filter-dropdown{
        position: relative;
        width: fit-content;
        color:$white;
        
        .dropdown-container{

            >li{
                display: flex;
                justify-content: space-between;

                >div{
                    margin-left: 10px;
                }
            }

            .dropdown{
                display: none;
            }

            .clicked{
                display: block;
                position: absolute;
                bottom: -260%;
                // width: 100%;
                left: -20px;
                width: 150px;
                background: $offBlack;
                z-index: 2;
                 
                li{
                    font-size: 0.8rem;
                    padding: 7px 12px;
                    border-radius: 9px;
                    transition: background 0.2s ease-out;
                }

                li:hover{
                    background: $highlightBlack;
                }
            }
        }
    }

    .tag{
        color:white;
        display: flex;
        align-items: center;

        .color-indicator{
            width: 20px;
            border-radius: 3px;
            margin-right: 8px;
            height: 100%;
            background: pink;
        }
    }

}

