.selection{
    display: flex;
    justify-content: space-between;
    
    label{
        font-weight: 600;
    }
    
    .check{
        margin-bottom:10px;
        input[type="checkbox"]{
            position: relative;
            appearance:none;
            height: 20px;
            width: 20px;
            background:transparent;
            border: 1px $highlightBlack solid;
        }

        input[type="checkbox"]::after{
            content:"\f00c";
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            font-size: 10px;
            width: 10px;
            height: 10px;
            position: absolute;
            top:0;
            left:0;
            right: 0;
            bottom: 0;
            margin: auto;
            display: none;
        }

        input[type="checkbox"]:checked{
            background:$primaryBlue
        }

        input[type="checkbox"]:checked::after{
            display: block;
        }
    }    
}

.secondary{
    label{
        opacity: 0.5;
    }
}