// filter checkboxes

.filter-notif{
    padding-bottom: 10px;
    .check{
        input[type="checkbox"] , input[type="checkbox"]:checked{
            border-radius:2px
        }
    }
}

.secondary-filter{
    border-top: 2px solid $highlightBlack;
    padding-top: 30px;
    margin-top: 30px;
    .check{
        input[type="checkbox"] , input[type="checkbox"]:checked{
            border-radius:100%;
        }
    }
}

.buttons{
    display: flex;
    >button{
        width: 50%;
        margin-right: 10px;
    }
}




.get-customers-container{
    .social-buttons{
        display: flex;
        margin-top:20px;

        button{
            width: calc(100% / 3);       
        }

        button:nth-child(even){
            margin:0px 10px;
        }
    }
}

